//importing font
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,900;1,400;1,700;1,900&display=swap");

//assigning variables
$primary: #202124;
$secondary: #fbbd05;
$primaryFont: "Roboto", sans-serif;
$border: 1px solid rgba(255, 255, 255, 0.5);

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: $primaryFont;
  background-color: $primary;
  color: whitesmoke;
}

//styling for header component
.header {
  nav {
    display: flex;
    width: 100%;
    flex-direction: row;
    h2 {
      margin: 1rem 0.5rem;
      font-weight: 400;
      text-align: left;
    }

    div {
      margin: 1rem 10%;
      font-weight: 800;
      text-align: right;
      font-size: larger;
    }
  }

  hr {
    border: $border;
  }
}

//styling for `input-section` component
.input-section {
  display: flex;
  flex-direction: column;
  margin: 2.5rem 25%;
  padding: 20px;
  border-radius: 0.7rem;
  border: $border;
  box-shadow: 2px whitesmoke;
  justify-content: space-around;
  position: relative;

  input,
  textarea {
    resize: vertical;
    font-family: $primaryFont;
    background-color: $primary;
    outline: none;
    border: none;
    font-size: large;
    color: whitesmoke;
  }

  button {
    position: absolute;
    bottom: 0;
    right: 0;
    border-radius: 100%;
    background-color: $secondary;
    font-size: large;
    outline: none;
    border: none;
  }
}

.notes-collection {
  display: flex;
  flex-wrap: wrap;
  margin: 0 10%;

  .notes {
    display: flex;
    flex-direction: column;
  }

  .note {
    padding: 0.5em 0.5rem 1rem 0.5rem;
    margin: 1rem;
    border-radius: 1em;
    border: $border;
    width: auto;
    min-width: 10rem;
    height: min-content;
    text-align: left;
    position: relative;
  }
}

@media screen and (max-width: 900px) {
  .input-section {
    text-align: center;
    margin: 1em auto;
    width: 85%;
  }

  .notes-collection {
    justify-content: center;
  }

  .notes {
    width: 100%;
  }

  .note {
    overflow: hidden;
  }
}
